@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap);
html {
  width: 100vw;
  height: 100vh; }

body {
  margin: 0;
  padding: 0;
  font-family: Helvetica, Ariel, sans-serif;
  width: 100%;
  height: 100%;
  font-size: 20px; }

@supports (-moz-appearance: none) {
  body {
    background-color: black; } }

#root {
  width: 100%;
  height: 100%; }

/* CSS Document */
/******************************
* TABLE OF CONTENTS:
* 1. UI Grid
* 2. Positioning
* 		2a. UI Row 1
* 		2b. UI Row 2
* 		2c. UI Row 3
* 		2d. UI Row 4
*		2e. Login
* 		2f. Print Mode
* 3. Alignment
* 4. SVG Microstate Styling
* 		4a. Available
* 		4b. Hover
* 		4c. Clicked
* 5. Drop Shadows
* 6. Dropdown Menus
* 		6a. Switch
* 		6b. Object Loader
*			i) Project Objects
*			ii) Project Object Details
*			iii) Thumbnails
*		6c. User Tag
*			i ) User Tag List
*			ii ) User Tag List Colouring
* 		6d. Layout Settings
* 			i) Layout Settings List
*		6e. Project Loader
* 		6f. Tools
* 			i) Tools List
* 7. Typography
* 8. Horizontal Rulers/Lines
* 9. Scrollbar Styling
* 10. Cursors
* 11. Inspectors
* 		11a. Basic
*		11b. Cam Path (Guest)
*		11c. Cam Path aka Tour Tool (Team Member)
*		11d. Colour Picker
*		11e. Volumetric Light Tool
*		11f. Video Media Object
* 12. Login
* 13. Print Mode
* 		13a. Legend Panel
* 		13b. Captions Panel
* 		13c. Print Aread Divider
* 14. SVG Animations
*		14a. Grouping
* 15. Mockup Styling

* NOTES:
* Padding, margins, border-radius, etc. are all based on the 52px circle grid (i.e. 1/4 of 52px = 13px);
******************************/
/********** 1. UI GRID **********/
body {
  margin: 0; }

.tour {
  border-radius: 4px;
  position: absolute;
  background-color: transparent;
  height: 30px;
  opacity: 1;
  z-index: 1001;
  pointer-events: none;
  transition: opacity 0.2s ease 0s;
  width: 30px; }

.tourPanel {
  cursor: pointer;
  height: 1050px;
  pointer-events: auto;
  bottom: 0px;
  left: 0px;
  overflow: hidden;
  position: absolute;
  right: 0px;
  top: 0px;
  z-index: 1000;
  mix-blend-mode: hard-light; }

.grid-container {
  width: 1400px;
  height: 1050px; }

.grid {
  position: relative;
  top: 0px;
  left: 0;
  display: grid;
  grid-template-columns: 24px repeat(52, 26px) 24px;
  grid-template-rows: 5px repeat(40, 26px) 5px; }

/********** 2. POSITIONING **********/
/*** 2a. UI Row 1 ***/
.goToDashboard {
  grid-row: 3/5;
  grid-column: 2/4; }

.project-loader {
  grid-row: 3/5;
  grid-column: 5/17;
  z-index: 500; }

.project-loaderShadowTog {
  /* used for shadows via JS only when dropdown is open */
  grid-row: 3/11;
  grid-column: 5/17;
  z-index: 500; }

.delete {
  grid-row: 3/5;
  grid-column: 18/20; }

.scale {
  grid-row: 3/5;
  grid-column: 21/23; }

.resetScale {
  grid-row: 3/5;
  grid-column: 23/25; }

.undo {
  grid-row: 3/5;
  grid-column: 26/28; }

.redo {
  grid-row: 3/5;
  grid-column: 28/30; }

.group {
  grid-row: 3/5;
  grid-column: 31/33; }

.homeView {
  grid-row: 3/5;
  grid-column: 34/36; }

.viewToggle {
  grid-row: 3/5;
  grid-column: 36/38; }

.userTag {
  grid-row: 3/5;
  grid-column: 39/51; }

.userTagShadowTog {
  /* used for shadows via JS only when dropdown is open */
  grid-row: 3/17;
  grid-column: 39/51; }

.layoutSettings {
  grid-row: 3/5;
  grid-column: 52/54; }

.layoutSettingsShadowTog {
  /* used for shadows via JS only when dropdown is open */
  grid-row: 3/9;
  grid-column: 52/54; }

/*** 2b. UI Row 2 ***/
.tools {
  grid-row: 6/8;
  grid-column: 2/4; }

.toolsShadowTog {
  /* used for shadows via JS only when dropdown is open */
  grid-row: 6/18;
  grid-column: 2/4; }

.objectLoader {
  grid-row: 6/8;
  grid-column: 5/17; }

.objectLoaderShadowTog {
  /* used for shadows via JS only when dropdown is open */
  grid-row: 6/22;
  grid-column: 5/17; }

.objectLoaderShadowTogDetails {
  /* used for shadows via JS only when dropdown is open */
  grid-row: 6/22;
  grid-column: 5/25; }

/*** 2c. UI Row 3 ***/
.north {
  grid-row: 9/12;
  grid-column: 2/4; }

/*** 2d. UI Row 4 ***/
.tooltips {
  grid-row: 39/41;
  grid-column: 2/4; }

.stateSwitch {
  grid-row: 39/41;
  grid-column: 20/36;
  z-index: 1; }

.switch-shadow-closed {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 52px; }

.switch-shadow-open {
  /* used for shadows via JS only when dropdown is open */
  height: 259px; }

.basic-inspector {
  grid-row: 34/41;
  grid-column: 39/52; }

.camPathGuest-inspector {
  grid-row: 26/41;
  grid-column: 39/52; }

.camPathMember-inspector {
  grid-row: 23/41;
  grid-column: 39/52; }

.colourPicker-inspector {
  grid-row: 27/41;
  grid-column: 39/52; }

.volumetric-inspector {
  grid-row: 21/41;
  grid-column: 39/52; }

.media-inspector {
  grid-row: 23/41;
  grid-column: 39/52; }

/*** 2e. Login ***/
.login {
  grid-row: 11/27;
  grid-column: 23/33; }

.overlay {
  grid-row: 1/43;
  grid-column: 1/55;
  background: #fff;
  opacity: 0.65;
  z-index: -1; }

.swoosh {
  grid-row: 33/43;
  grid-column: 1/55; }

/*** 2e. Print Mode ***/
.legend {
  grid-row: 3/29;
  grid-column: 43/52; }

.captions {
  grid-row: 30/37;
  grid-column: 12/52; }

.print-area {
  grid-row: 37/39;
  grid-column: 3/52; }

.compass {
  grid-row: 35/37;
  grid-column: 3/6; }

.legend-btn {
  grid-row: 39/41;
  grid-column: 45/47; }

.captions-btn {
  grid-row: 39/41;
  grid-column: 47/49; }

.print {
  grid-row: 39/41;
  grid-column: 50/52; }

/********** 3. ALIGNMENT **********/
.right {
  float: right; }

.left {
  float: left; }

.pad-right {
  padding-right: 13px; }

.pad-right-half {
  padding-right: 26px; }

/********** 4. SVG MICROSTATE STYLING **********/
/*** 4a. Available ***/
svg .btn-bg {
  fill: #fff; }

svg .btn-icon-fill {
  /* dark icon on light background */
  fill: #333; }

svg .btn-icon-fill-dark {
  /* light icon on dark background */
  fill: #fff; }

svg .btn-icon-stroke {
  /* dark icon on light background */
  stroke: #333; }

svg .btn-icon-stroke-dark {
  /* light icon on dark background */
  stroke: #fff; }

/*** 4b. Hover ***/
svg:hover .btn-icon-fill,
svg:hover .btn-icon-fill-dark {
  fill: #ffc9af; }

svg:hover .btn-icon-stroke,
svg:hover .btn-icon-stroke-dark {
  stroke: #ffc9af; }

/*** 4c. Clicked ***/
svg:active .btn-icon-fill,
svg:active .btn-icon-fill-dark {
  fill: #ff8672; }

svg:active .btn-icon-stroke,
svg:active .btn-icon-stroke-dark {
  stroke: #ff8672; }

/********** 5. DROP SHADOWS **********/
.shadow {
  border-radius: 26px;
  border: transparent;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
  z-index: -1; }

.shadow-left {
  border-radius: 26px 0 0 26px;
  border: transparent;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
  z-index: -1; }

.shadow-right {
  border-radius: 0 26px 26px 0;
  border: transparent;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
  z-index: -1; }

/********** 6. DROPDOWN MENUS **********/
.dropdown {
  position: relative;
  display: inline-block; }

.dropdown-btn {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 13px 26px;
  color: #333;
  background-color: #fff;
  border: transparent;
  border-radius: 26px;
  text-align: left;
  line-height: 26px;
  outline: none;
  cursor: pointer; }

.dropdown-btn svg {
  display: inline-block;
  vertical-align: middle; }

.dropdown-btn-text {
  margin: 0; }

/*** 6a. Switch ***/
.switch-btn-mod {
  color: #fff;
  background-color: #333;
  border-radius: 0 0 26px 26px; }

.switch-content {
  position: absolute;
  display: none;
  width: 390px;
  padding: 13px 13px 26px 13px;
  background-color: #333;
  border: transparent;
  border-radius: 26px 26px 0 0; }

.switch-content a {
  display: block;
  padding: 13px;
  color: #fff;
  text-decoration: none; }

.switch-content a:hover {
  background-color: #4d4d4d;
  border: transparent;
  border-radius: 26px; }

.show-switch-content {
  display: block;
  bottom: 100%;
  /*Open upwards*/ }

.hide {
  /* Class is added/removed via JS */
  display: none; }

.show {
  /* Class is added/removed via JS */
  display: block;
  /* */ }

/*** 6b. Object Loader ***/
.objectLoader-btn-mod {
  color: #fff;
  background-color: #333;
  border-radius: 26px 26px 0 0; }

.objectLoader-content {
  position: absolute;
  display: none;
  width: 286px;
  height: 351px;
  padding: 0 13px 13px;
  background-color: #333;
  border: transparent;
  border-radius: 0 0 26px 26px; }

.show-objectLoader-content {
  display: block; }

.line-wrapper {
  width: 286px; }

/* 6b. i) Project Objects */
#projectObjects {
  width: 286px;
  height: 36px;
  margin: 8px 0;
  padding: 0 13px 0 62px;
  /*36px circle + 13px pad right + 13px pad left = 62px*/
  background-color: #4d4d4d;
  background-image: url(../../static/media/search.1ad5ce98.svg);
  background-position: 13px center;
  background-repeat: no-repeat;
  border: transparent;
  border-radius: 18px;
  box-sizing: border-box;
  outline: none; }

#projectObjects::-webkit-input-placeholder {
  color: #b4b4b4;
  font-size: 14px;
  font-style: italic; }

#projectObjects::-ms-input-placeholder {
  color: #b4b4b4;
  font-size: 14px;
  font-style: italic; }

#projectObjects::placeholder {
  color: #b4b4b4;
  font-size: 14px;
  font-style: italic; }

#projectObjects:valid {
  color: #fff; }

.object-list-head {
  width: 260px;
  height: 36px;
  margin: 8px 0;
  padding: 0 13px; }

.object-list-head h1 {
  margin: 0;
  color: #fff;
  line-height: 36px; }

.object-list-wrapper {
  width: 286px;
  height: 247px;
  overflow-x: hidden;
  overflow-y: auto; }

.object-list-wrapper::-webkit-scrollbar {
  /*display: none;*/ }

.object-list-wrapper ul {
  padding: 0;
  margin: 0;
  list-style-type: none; }

.object {
  width: 286px;
  height: 36px;
  margin: 0;
  padding: 0 13px 0 13px;
  cursor: pointer;
  box-sizing: border-box; }

.object p {
  width: calc(100% - 85px);
  /* 100% - (2 * 36px icons) - 13px pad) */
  margin: 0;
  padding: 0;
  color: #fff;
  line-height: 36px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap; }

.object:hover {
  background-color: #4d4d4d;
  border: transparent;
  border-radius: 18px; }

.object-icon {
  width: 36px;
  height: 36px; }

.pinned {
  fill: #fff; }

.unpinned {
  fill: #333; }

/* 6b. ii) Project Object Details */
.object-details-wrapper {
  position: absolute;
  top: 0;
  left: 299px;
  width: 208px;
  height: 351px; }

.thumbnail {
  width: 104px;
  height: 104px;
  margin: 8px auto 26px auto;
  background-color: #fff;
  background-position: center center;
  background-size: 104px auto;
  background-repeat: no-repeat;
  border: transparent;
  border-radius: 13px; }

.object-name {
  padding: 0 26px;
  height: 52px; }

.object-name p {
  display: -webkit-box;
  width: 100%;
  margin: 0;
  color: #fff;
  font-weight: 700;
  line-height: 21px;
  text-align: center;
  text-overflow: ellipsis;
  overflow: hidden;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  white-space: normal; }

.details {
  padding: 0 26px 0 52px;
  height: 161px;
  color: #b4b4b4;
  overflow-x: hidden;
  overflow-y: auto; }

.details ul {
  list-style-type: disc; }

/* 6b. iii) Thumbnails */
.chair {
  background-image: url(../../static/media/chair.7f5c4703.jpg); }

.table {
  background-image: url(../../static/media/table.7c78980e.jpg); }

.truss {
  background-image: url(../../static/media/truss.fba0429a.jpg); }

.pipeAndDrape {
  background-image: url(../../static/media/pipeAndDrape.0f3ddf4d.jpg); }

/*** 6c. User Tag ***/
.user-dropdown-btn {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0 13px;
  color: #333;
  background-color: #79dbf8;
  border: transparent;
  border-radius: 26px;
  text-align: left;
  outline: none;
  cursor: pointer; }

.userTag-btn-mod {
  color: #fff;
  background-color: #333;
  border-radius: 26px 26px 0 0; }

/* 6c. i) User Tag List */
.userTag-content {
  position: absolute;
  top: 52px;
  display: none;
  width: 286px;
  padding: 0 13px 7.5px;
  background-color: #333;
  border: transparent;
  border-radius: 0 0 26px 26px; }

.user-wrapper {
  max-height: 304.5px;
  /* (37px user tags * 6) + (15px margin bottom * 6) = 312 - 7.5px padding = 304.5 */
  overflow-x: hidden;
  overflow-y: auto; }

.show-userTag-content {
  display: block; }

.yourUserName {
  height: 37px;
  margin: 0;
  padding: 0 13px;
  border: transparent;
  border-radius: 26px; }

.userName {
  display: flex;
  align-items: center;
  height: 37px;
  margin: 15px 0;
  padding: 0 13px;
  border: transparent;
  border-radius: 26px; }

.yourUserName p,
.userName p {
  margin: 0;
  padding: 0;
  color: #333;
  line-height: 37px;
  white-space: nowrap; }

.userName:first-of-type {
  /*Half the margin-top is already set by .userTag-content*/
  margin-top: 7.5px; }

.userName:last-of-type {
  /*The bottom margin is already set by .userTag-content*/
  margin-bottom: 0px; }

.userName-icon {
  display: block;
  padding-right: 13px; }

/* 6c. ii) User Tag List Colouring */
.purple {
  background-color: #8f7ad8; }

.green {
  background-color: #8ceaa7; }

.yellow-green {
  background-color: #c3d670; }

.orange {
  background-color: #efb16f; }

.red {
  background-color: #ff6f80; }

.pink {
  background-color: #ef84db; }

.blue {
  background-color: #79dbf8; }

/*** 6d. Layout Settings ***/
.settings-dropdown-btn {
  width: 100%;
  height: 100%;
  border: transparent;
  border-radius: 26px;
  outline: none;
  cursor: pointer; }

.settings-btn-mod {
  background-color: #333;
  border-radius: 26px 26px 0 0; }

.setting-states {
  position: absolute;
  top: 0;
  left: 0;
  width: 52px;
  height: 52px;
  margin: 0;
  padding: 0; }

/* 6d. i) Layout Settings List */
.layoutSettings-content {
  position: absolute;
  top: 52px;
  display: none;
  width: 52px;
  background-color: #333;
  border: transparent;
  border-radius: 0 0 26px 26px; }

.show-layoutSettings-content {
  display: block; }

.setting {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 52px;
  border: transparent;
  cursor: pointer; }

.setting:last-of-type {
  border-radius: 0 0 26px 26px; }

.setting-icon {
  display: block; }

.setting-icon-bg {
  fill: transparent; }

.setting-icon:hover .setting-icon-bg {
  fill: #4d4d4d; }

/*** 6e. Project Loader ***/
#projectLoader-btn {
  cursor: default; }

.projectLoader-txt-wrap {
  height: 26px;
  width: 234px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap; }

.projectLoader-btn-mod {
  color: #fff;
  background-color: #333;
  border-radius: 26px 26px 0 0; }

.projectLoader-content {
  position: absolute;
  display: none;
  width: 286px;
  height: 131px;
  padding: 13px;
  background-color: #333;
  border: transparent;
  border-radius: 0 0 26px 26px; }

.show-projectLoader-content {
  display: block; }

.project,
.layout {
  width: 260px;
  height: 36px;
  margin: 0;
  padding: 0 13px;
  color: #fff;
  line-height: 36px;
  cursor: pointer; }

.project:hover,
.layout:hover {
  background-color: #4d4d4d;
  border: transparent;
  border-radius: 26px; }

.project-name,
.layout-name {
  width: 221px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap; }

.project-list-wrapper,
.layout-list-wrapper {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  width: 286px;
  min-height: 39px;
  /* 1 circle tall (52px) minus 13px bottom padding from projectLoader-content */
  overflow-x: hidden;
  overflow-y: auto; }

.project-list-wrapper {
  max-height: 130px;
  /* 3 circles tall (156px) minus 13px top/bottom padding from projectLoader-content */ }

.layout-list-wrapper {
  max-height: 91px;
  /* 2 circles tall (108px) minus 13px bottom padding from projectLoader-content */ }

.project-list-wrapper ul,
.layout-list-wrapper ul {
  list-style-type: none;
  margin: 0;
  padding: 0 26px 0 0;
  width: 100%; }

.refresh-icon {
  display: block; }

/*** 6f. Tools ***/
.tools-dropdown-btn {
  width: 100%;
  height: 100%;
  border: transparent;
  border-radius: 26px;
  outline: none;
  cursor: pointer; }

.tools-btn-mod {
  background-color: #333;
  border-radius: 26px 26px 0 0; }

.tools-states {
  position: absolute;
  top: 0;
  left: 0;
  width: 52px;
  height: 52px;
  margin: 0;
  padding: 0; }

/* 6f. i) Tools List */
.tools-content {
  position: absolute;
  top: 52px;
  display: none;
  width: 52px;
  background-color: #333;
  border: transparent;
  border-radius: 0 0 26px 26px; }

.show-tools-content {
  display: block; }

.tool {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 52px;
  border: transparent;
  cursor: pointer; }

.tool:last-of-type {
  border-radius: 0 0 26px 26px; }

.tool-icon {
  display: block; }

.tool-icon-bg {
  fill: transparent; }

.tool-icon:hover .tool-icon-bg {
  fill: #4d4d4d; }

/********** 7. TYPOGRAPHY **********/
