  line-height: 19.5px; }

/********** 8. HORIZONTAL RULERS/LINES **********/
hr {
  margin: 0 13px 13px;
  border: 0.5px solid #c9c9c9; }

.objectLoader hr,
.project-loader hr,
.camPathList-innerContainer hr,
.media-innerContainer hr {
  margin: 0px 13px;
  border: 0.5px solid #c9c9c9; }

.camPathGuest-rowTop,
.camPathMember-rowTop,
.media-rowTop,
.volumetric-rowTop {
  margin: 0 26px; }

.camPathTools {
  margin: 0 26px; }

.print-area-divider hr {
  padding: 0;
  margin: 24px 0;
  border: 2px dashed #333; }

/********** 9. Scrollbar Styling **********/
::-webkit-scrollbar {
  background: transparent;
  width: 4px; }

::-webkit-scrollbar-thumb {
  background: #fff;
  border-radius: 2px; }

/********** 10. CURSORS **********/
.pointer {
  cursor: pointer; }

.grab {
  cursor: -webkit-grab;
  cursor: grab; }

/********** 11. INSPECTORS **********/
/*** 11a. Basic ***/
.basic-wrapper {
  position: relative;
  width: 100%;
  background-color: #333;
  border-radius: 26px; }

.basic-inspector span {
  position: relative;
  top: 50%;
  width: 52px;
  margin: 0;
  padding: 0;
