/********** 14. SVG ANIMATIONS **********/
/*** 14a. Grouping ***/
/* Draw box on hover */
.solid-box {
  stroke: #333;
  stroke-width: 2px;
  stroke-dasharray: 5 200; }

@-webkit-keyframes drawSolidBox {
  0% {
    stroke-width: 2px;
    stroke-dasharray: 5 200; }
  100% {
    stroke-width: 2px;
    stroke-dasharray: 104; } }

@keyframes drawSolidBox {
  0% {
    stroke-width: 2px;
    stroke-dasharray: 5 200; }
  100% {
    stroke-width: 2px;
    stroke-dasharray: 104; } }

svg:hover rect#box.solid-box {
  stroke: #ffc9af;
  -webkit-animation: 0.5s drawSolidBox linear forwards; }

svg:active rect#box.solid-box {
  stroke: #ff8672; }

/* Scale shapes on active */
.scale-animate {
  transition: transform 0.2s linear;
  transform-origin: 50% 50%; }

svg:active .scale-animate {
  transform: scale(1.2); }

